<template>
  <div id="app">
    <v-container>
      <v-layout row >
        <v-card style="margin: 30px auto; width: 500px;">
          <v-card-title primary-title>
            <h4>Login</h4>
          </v-card-title>
          <v-form style="margin: 15px">
            <v-text-field
              prepend-icon="mdi-phone"
              name="Phone"
              label="Telefono"
              v-model="phone"
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-lock"
              name="Password"
              label="Contraseña"
              type="password"
              v-model="password"
            ></v-text-field>
            <v-card-actions>
              <v-btn @click="login()" color="primary" large block>Login</v-btn>
            </v-card-actions>
            <br/>
            <span>No tengo cuenta, deseo <router-link to="register">registrarme</router-link></span>
          </v-form>
        </v-card>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: "Login",

  data: () => ({
    phone: '',
    password: '',

    server: 'http://localhost:9292'
  }),

  methods: {
    login(){
      axios({
        method: "POST",
        url: this.server + "/api/v1/users/login",
        data: { phone: this.phone, password: this.password }
      }).then(
        (response) => {
          if (response.data.status == 'success') {
            let token =  response.data.data
            this.$store.dispatch('login', token);
            this.$router.push('products');
          }else{
            console.log('fallo');
          }
        },
        (response) => {
          console.log(response);
        }
      );
    }
  }
};
</script>
