import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: ''
  },
  mutations: {
    logOut: state => {
      state.token = '';
    },
    logIn: (state, token) => {
      state.token = token;
    },
  },
  actions: {
    logout: context => {
      context.commit('logOut')
    },
    login: (context, token) => {
      context.commit('logIn', token)
    }
  },
  getters: {
    token: state => {
      if (state.token !== null) return state.token;
      else return '';
    },

  },
  modules: {
  }
})
